.dtWrapper {
  margin: auto;
  width: 310px;
  position: relative; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #009a17;
  padding: 1.5rem 1rem;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem; }
  .header .header-date {
    display: flex;
    color: white;
    font-size: 1.125rem;
    justify-content: center;
    flex-wrap: wrap; }
    .header .header-date .header-date--month {
      margin-left: 0.5rem;
      cursor: pointer; }
    .header .header-date .header-date--year {
      cursor: pointer; }
    .header .header-date .header-date--day {
      font-size: 24px;
      text-align: center;
      width: 100%;
      margin-top: 0.5rem; }
  .header .header--btn {
    cursor: pointer;
    display: block;
    width: 1.25rem;
    height: 1.25rem;
    color: white; }
    .header .header--btn.is-disabled {
      opacity: 0.2;
      pointer-events: none; }

.is-rtl .header .header-date {
  direction: ltr; }

.is-rtl .header .header--btn {
  transform: scale(-1); }

.yearWrapper {
  position: relative; }

.yearWrapper:after,
.yearWrapper:before {
  content: '';
  display: block;
  width: 100%;
  height: 1.875rem;
  position: absolute;
  left: 0;
  transition: 0.4s;
  transition-delay: 0.2s; }

.yearWrapper:before {
  background-image: linear-gradient(0deg, #fff, #fff 10%, rgba(245, 245, 245, 0));
  bottom: 0; }

.yearWrapper:after {
  background-image: linear-gradient(180deg, #fff, #fff 10%, rgba(245, 245, 245, 0));
  top: -0.1em; }

.yearList {
  list-style: none;
  overflow: hidden;
  padding: 0;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  height: 18.1875rem;
  overflow-y: scroll;
  padding: 1.875rem 0;
  margin: 0.5rem 0; }

.yearList::-webkit-scrollbar {
  display: none; }

.yearList_year {
  width: 100%;
  height: 3.4375rem;
  line-height: 3.4375rem;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer; }

.is-selectedYearRef {
  color: #03bf1f;
  font-weight: bold;
  font-size: 1.375rem; }

.yearList_year:nth-child(5n + 1) {
  clear: left; }

.monthList {
  list-style: none;
  overflow: hidden;
  padding: 0;
  text-align: center;
  will-change: transform;
  transition: 800ms; }

.monthList_month {
  width: calc(310px / 3);
  height: 4.5rem;
  line-height: 4.5rem;
  float: left;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer;
  color: #666;
  transition: color 200ms ease-in-out; }
  .monthList_month:hover {
    color: #444; }
  .monthList_month.is-disabled {
    opacity: 0.2;
    pointer-events: none; }
  .monthList_month.is-selected-month {
    color: #03bf1f;
    font-weight: bold; }

.monthList_month:nth-child(3n + 1) {
  clear: left; }

.is-rtl.monthList .monthList_month {
  float: right; }

.is-rtl.monthList .monthList_month:nth-child(3n + 1) {
  clear: right; }

.daysList {
  list-style: none;
  overflow: hidden;
  padding: 0.5rem 0;
  margin: 0;
  text-align: center; }
  .daysList .daysList-day {
    width: calc(310px / 7);
    height: calc(310px / 7);
    line-height: calc(310px / 7);
    float: left;
    text-align: center;
    box-sizing: border-box;
    cursor: pointer;
    border-radius: 50%;
    color: #666;
    font-size: 0.875rem;
    margin: 0.0625rem 0; }
  .daysList .is-week-days {
    color: #aaa;
    font-size: 14px;
    font-weight: 500;
    height: 34px;
    line-height: 34px;
    border-radius: 0; }
  .daysList .is-prev-month {
    border-radius: 0; }
  .daysList .is-next-month {
    border-radius: 0; }
  .daysList .is-border-right-0 {
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%; }
  .daysList .is-border-left-0 {
    border-top-left-radius: 50%;
    border-bottom-left-radius: 50%; }
  .daysList .daysList-day:nth-child(7n + 1) {
    clear: left; }
  .daysList .is-today {
    box-shadow: inset 0px 0px 0px 2px #029418; }
  .daysList .is-disabled {
    pointer-events: none;
    background-color: #e4ebe5;
    color: #aebfa9; }
  .daysList .is-disabled-by-user {
    pointer-events: none;
    opacity: 0.2; }
  .daysList .is-minMaxDisabled {
    pointer-events: none;
    opacity: 0.2; }
  .daysList .is-selected-day {
    background-color: #03bf1f;
    color: white; }
  .daysList .is-selected-day-from {
    background-color: #03bf1f;
    border-radius: 0;
    border-top-left-radius: 50%;
    border-bottom-left-radius: 50%;
    color: white; }
  .daysList .is-selected-day-to {
    background-color: #03bf1f;
    border-radius: 0;
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
    color: white; }
  .daysList .is-selected-day-from.is-selected-day-to {
    border-radius: 50%; }
  .daysList .is-selected-day-range {
    color: #008213;
    border-radius: 0;
    background-color: #a0f5ad; }
  .daysList .is-pointer {
    cursor: pointer; }
  .daysList .is_weekends {
    color: #d20000 !important; }

.is-rtl.daysList .daysList-day {
  float: right; }

.is-rtl.daysList .daysList-day:nth-child(7n + 1) {
  clear: right; }

.is-rtl.daysList .is-selected-day-from {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%; }

.is-rtl.daysList .is-selected-day-to {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%; }

.is-rtl .daysList .is-border-right-0 {
  border-radius: 0;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%; }

.is-rtl .daysList .is-border-left-0 {
  border-radius: 0;
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%; }

.is-rtl .daysList .is-selected-day-from {
  border-radius: 0;
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%; }

.is-rtl .daysList .is-selected-day-to {
  border-radius: 0;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%; }

.calender-modal {
  position: absolute;
  background-color: #fff;
  z-index: 10;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.25);
  font-family: inherit;
  border-radius: 0.25rem;
  overflow: hidden;
  width: 310px; }
  .calender-modal.is-calender {
    position: relative; }

.input-picker {
  position: relative;
  display: inline-block; }
  .input-picker .input-picker--input {
    text-align: center;
    line-height: 1.625rem;
    height: 1.625rem;
    padding: 0 8px;
    border-radius: 0.25rem;
    border-width: 0.0625rem; }
    .input-picker .input-picker--input:focus {
      outline: none; }
  .input-picker .input-picker--clearBtn {
    display: block;
    width: 1.25rem;
    height: 1.25rem;
    position: absolute;
    top: 50%;
    right: 0.3125rem;
    transform: translateY(-50%);
    cursor: pointer;
    color: #c9c9c9; }

.todayBtn {
  display: block;
  text-align: center;
  margin: auto;
  border-radius: 16px;
  border: 0.0625rem solid #009a17;
  width: 5rem;
  padding: 0.25rem 0;
  transition: color 200ms ease-in-out, background-color 200ms ease-in-out;
  font-size: 0.875rem;
  font-weight: 400;
  margin-bottom: 0.25rem;
  cursor: pointer; }

